*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Noto Sans TC', sans-serif;
}
    

a{
    text-decoration: none;
    color: black;
}

ul{
    list-style-type: none;
}

.morePicture{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4rem;

    button{
        padding: 0.25rem 0.5rem;
        font-size: 2rem;
        border: none;
        background-color: rgb(73, 0 ,73);
        color: white;
        cursor: pointer;
    }
}

@import "./nav";
@import "./search";
@import "./footer";
@import "./picture";